@font-face {
	font-family: 'Rubik Mono One';
	src: url('../../font/RubikMonoOne-Regular.eot');
	src: url('../../font/RubikMonoOne-Regular.eot?#iefix') format('embedded-opentype'),
			url('../../font/RubikMonoOne-Regular.woff2') format('woff2'),
			url('../../font/RubikMonoOne-Regular.woff') format('woff'),
			url('../../font/RubikMonoOne-Regular.ttf') format('truetype'),
			url('../../font/RubikMonoOne-Regular.svg#RubikMonoOne-Regular') format('svg');
	font-weight: normal;
	font-style: normal;
}

