			url('../../font/Questrial-Regular.woff2') format('woff2'),
			url('../../font/Questrial-Regular.woff') format('woff'),
			url('../../font/Questrial-Regular.ttf') format('truetype'),
			url('../../font/Questrial-Regular.svg#Questrial-Regular') format('svg');
	font-weight: normal;
	font-style: normal;
}

